.square {
    width: 50px;
    height: 50px;
    padding: 0;
    font-family: 'Times New Roman', Times, serif;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    float: left;
    background: #fff;
    border: 1px solid #999;
    border-radius: 5px;
}

.square:hover {
    background: #ddd;
}