.game {
  width: 100%;
  height: 100vh;
  margin: 20vh auto 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  /* align-items: center; */
}

.game-board {
    /* height: 100vh; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.game-info {
  width: 100%;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 24px;
}

.moveButton {
  width: 200px;
  padding: 2px;
  font-size: 18px;
  font-family: 'Playpen Sans', cursive;
  color: #333;
  background: #FFC300;
  border: none;
  border-radius: 20px;
  box-shadow: 3px 3px 1px #555;
}